import { Component } from 'react';
import './Portfolio.css';

let CMP

class Portfolio extends Component {
    constructor (props) {
        super(props);
        CMP = this
        this.state = { index : 0, mobi : 0 }
        this.itemJump = this.itemJump.bind(this);
        this.itemOpen = this.itemOpen.bind(this);
    };

    itemOpen() {
        window.open(structure[this.state.index].link);
    }

    callOpen() {
        window['ScrollJS'].setThis('.ContactRoot')
    }
    
    itemJump(e) {
        let _nval = ArrayOut(4, this.state.index + parseInt(e.target.lang));
        this.setState({ index : _nval }, function() {
            updateVideoTransforms(_nval)
        });
    }

    buildVideoItems() {
        let out = [];
        Object.values(structure).forEach(function(obj, i) {
            out.push(
                <video
                    src={obj.play}
                    autoPlay={true}
                    loop={true}
                    key={i}
                    lang={i}
                    className={"v" + (i + 1)}
                    muted={true}
                    onClick={openThis}
                />
            );
        });
        return out;
    };
    
    getPortfolioName() {
        let obj = structure[this.state.index];
        return (
            <div className="PortfolioName">
                {obj.name}
            </div>
        );
    }
    
    getPortfolioInfo() {
        let obj = structure[this.state.index];
        return ([
            <div className="PortfolioText" key={1}>
                {obj.info}
            </div>,
            <button onClick={this.itemOpen} className="PortfolioDemoButton"
                key={2}>View Portfolio</button>,
                <button onClick={this.callOpen} className="PortfolioCallButton"
                    key={3}>Contact Us</button>
        ]);
    }

    itemOpenMobi(e) {
        window.open(structure[e.target.lang].link)
    }

    buildItems() {
        let arr = [];
        let _this = this
        structure.forEach(function(obj, i) {
            arr.push(
                <div
                    className="PortfolioItem"
                    key={i}
                    style={ { backgroundImage : 'url('+ obj.snap +')' } }
                >
                    <div
                        className="PortfolioItemTitle"
                        onClick={ _this.itemOpenMobi }
                        lang={i}
                    >{ obj.name }</div>
                </div>
            );
        });
        return arr;
    };

	render() {

        if(window.innerWidth < window.innerHeight) {
            return (
                <div className="PortfolioRoot">
                    <div className="PortfolioTitle">
                        Experience Portfolio
                    </div>
                    <div className="PortfolioInner">
                        { this.buildItems() }
                    </div>
                </div>
            );
        }

		return (
            <div className="PortfolioRoot">
                <div className="PortfolioRootInner">
                    <div className="PortfolioTitle">
                        Experience Portfolio
                    </div>
                    <div className="PortfolioInner">
                        <div className="PortfolioInfo">
                             { this.getPortfolioName() }
                             { this.getPortfolioInfo() }
                        </div>

                        <button
                            className="PortfolioButton PortfolioButtonBack"
                            onClick={this.itemJump} lang="-1">
                        </button>
                        <div className="PortfolioPlay PortfolioPlayState_1">
                            { this.buildVideoItems() }
                        </div>
                        <button
                            className="PortfolioButton PortfolioButtonNext"
                            onClick={this.itemJump} lang="+1">
                        </button>

                    </div>
                </div>
            </div>
        );

	};

};

let ArrayOut = function(len, val) {
    if(val < 0) { return len; }
    else if(val > len) { return 0; }
    else { return val; }
}

function updateVideoTransforms(d) {
    let gZ = 1;
    let gY = -3;
    let tZ = (gZ * -d);
    let tY = (gY * -d)
    Array.from(document.querySelectorAll('.PortfolioPlay > video')).forEach(function(e, i, r) {
        if(i === d) {
            e.style.filter = 'blur(0px) grayscale(0%)';
            e.style.opacity = 1;
            e.play();
        } else {
            e.style.filter = 'blur(1px) grayscale(0%)';
            e.pause();
        }
        
        if(i < d) {
            // hide
            e.style.opacity = 0;
            setTimeout(() => { e.style.width = '20px' }, 300)
        } else {
            e.style.width = '45vw'
        }

        if(i > d) {
            // behind
            e.style.opacity = 0.4;
        }

        e.style.zIndex = (r.length - i);
        e.style.transform =
        'translateX('+ tZ +'vw) translateY('+ tY +'vh)';

        tZ += gZ;
        tY += gY;
    });
}

