window.addEventListener('load', function() {
    updateVideoTransforms(0);
});

let openThis = (evt) => {
    let obj = structure[evt.target.lang]
    if(obj.link) { window.open(obj.link) }
}

let last = 0

window.addEventListener('resize', () => {
    updateVideoTransforms(CMP.state.index)
    if(window.innerWidth > window.innerHeight && last === 1) {
        last = 0
        CMP.setState({ mobi : Date.now() })
        return
    }
    if(window.innerWidth < window.innerHeight && last === 0) {
        last = 1
        CMP.setState({ mobi : Date.now() })
        return
    }
})

export default Portfolio;