import { Component } from 'react';
import './Blog.css';
import BloggerAPI from '../scripts/bloggerAPI';

class Blog extends Component {

	render() {
		return (
            <div className="BlogRoot">
            <div className="BlogRootInner">
                <div className="BlogImage"></div>
                <div className="BlogButton">Read Now!</div>
                <div className="BlogInner">
                    <div className="BlogInnerTitle">Read WynkAR Blog...</div>
                    <div className="BlogInnerTray"></div>
                </div>
            </div>
            </div>
        );
	};

};

let api = new BloggerAPI('791479127391728969');

window.addEventListener('load', function() {
    // get latest 3 posts
    api.requestSummaryList(0, 3, function(obj) {
        if(obj.feed && obj.feed.entry) {
            buildBlogPostList(obj.feed.entry)
        }
    });
    // button
    document.querySelector('.BlogButton').addEventListener('click', function() {
        window.open('blog/');
    });
});

function buildBlogPostList(arr) {
    let out = '';
    // each item
    arr.forEach(function(obj) {
        let img = '';
        if(obj.author[0].gd$image) { img = obj.author[0].gd$image.src; }
        let pid = obj.id.$t.split('-').pop();
        out += `<div class="BlogItem" onclick="openBlogPost('`+ pid +`')">`;
        out += '<div class="BlogItemTitle">'+ obj.title.$t +'</div>';
        out += '<div class="BlogItemText">'+ shortForm(obj.summary.$t) +'</div>';
        out += '<div class="BlogItemAuth" ';
        out += 'style="background-image: url('+ img +');">';
        out += '</div>';
        out += '<div class="BlogItemAuthName">';
        out += obj.author[0].name.$t +'<br>'+ obj.updated.$t.split('T')[0];
        out += '</div>';
        out += '</div>';
    });
    // visit blog item
    out += `<div class="BlogItem BlogItemMore" onclick="window.open('blog/')">`;
    out += '<div class="BlogItemTitle BlogItemTitleMore">More posts...</div>';
    out += '</div>';

    document.querySelector('.BlogInnerTray').innerHTML += out;

};

function shortForm(txt) {
    return txt + ' ...<b>Read More</b>';
};

window['openBlogPost'] = function(id) {
    window.open('blog/post.html?id=' + id);
};

export default Blog;