import {
    _args
} from '../import';

// ==========================================================================

let findAll = function() {
    var args = _args(arguments);
    var model = args.obj[0];
    var out = [];
    function recMesh(obj) {
        out.push(obj);
        if(obj.children !== undefined) {
            for(var i = 0; i < obj.children.length; i++) {
                recMesh(obj.children[i]);
            }
        }
    }; recMesh(model);
    return out;
};

let findById = function() {
    var args = _args(arguments);
    var model = args.obj[0];
    var name  = args.str[0];
    var mult  = args.boo[0];

    var out = [];
    function recMesh(obj) {
        if(obj.uuid === name) {
            out.push(obj);
        }
        if(obj.children !== undefined) {
            for(var i = 0; i < obj.children.length; i++) {
                recMesh(obj.children[i]);
            }
        }
    }; recMesh(model);

    if(mult) { return out; }
    else { return out[0]; }
};

