let findByProperty = function() {
    var args = _args(arguments);
    var model = args.obj[0];
    var data  = args.obj[1];
    var mult  = args.boo[0];

    var out = [];
    function recMesh(obj) {
        Object.keys(data).forEach(function(key) {
            if(obj[key] === data[key]) { out.push(obj); }
        });
        if(obj.children !== undefined) {
            for(var i = 0; i < obj.children.length; i++) {
                recMesh(obj.children[i]);
            }
        }
    }; recMesh(model);

    if(mult) { return out; }
    else { return out[0]; }
};

let findByMaterialProperty = function() {
    var args = _args(arguments);
    var model = args.obj[0];
    var data  = args.obj[1];
    var mult  = args.boo[0];

    var out = [];
    function recMesh(obj) {
        if(obj.material !== undefined) {
            Object.keys(data).forEach(function(key) {
                if(obj.material[key] === data[key]) { out.push(obj); }
            });
        }
        if(obj.children !== undefined) {
            for(var i = 0; i < obj.children.length; i++) {
                recMesh(obj.children[i]);
            }
        }
    }; recMesh(model);

    if(mult) { return out; }
    else { return out[0]; }
};

// ==========================================================================

export {
    findAll,
    findById,
    findByName,
    findByType,
    findByProperty,
    findByMaterialName,
    findByMaterialProperty
};